.NavMain {
  background-color: rgba(0, 9, 134, 0.774);
  color:rgb(255, 255, 255);
  display: flex;
  align-items: center;
  padding: 20px;
  height: 70px;
  border-bottom-right-radius: 10px ;
  border-bottom-left-radius: 10px ;
}

.NavMain .nav-log {
  margin-left: auto;
  display: flex;
  color: rgb(255, 255, 255);
}
.NavMail .nav-trip {
  margin-right: auto;
  display: flex;
}

.NavMain .nav-list li:not(:last-child) {
  margin-right: 20px;
}

.NavMain img {
  width: 100px;
}


.home {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    justify-content: space-evenly;
    background-image: url("https://thepostcalvin.com/wp-content/uploads/2016/02/map-with-pins-for-GV.jpg");
    background-size: cover;
    background-position: bottom;
    padding: 30px;
    height: 400px;
    margin: 10px 0px;
    border: 1px solid rgb(156, 156, 156)
}

.home p {
    font-weight: bold;
    font-size: 25px;
    font-family: 'Yanone Kaffeesatz', sans-serif;
    color: black;
    text-shadow: white  2px 0 5px;
}

.home h1 {
    font-size: 50px;
    color:#0800a3;
    text-shadow: #f1c232ff  2px 0 10px;
    font-family: 'Yanone Kaffeesatz', sans-serif;

}
.image-line {
    display: flex;
    justify-content: center;
    text-align: center;
    color: white;
    margin: 10px 0px;
    border-radius: 10px;

}
.image-line div {
    font-family: 'Yanone Kaffeesatz', sans-serif;
    font-size: 60px;
    padding: 200px 75px;
    width: 100%;
    font-weight: bold;
    text-shadow: rgb(0, 0, 0) 5px 5px 5px;
    background-image: url(/images/Berlin-barcelona-paris-london.JPG);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}


.form {
    background-color: rgba(255, 255, 255, 0.85);
    display: flex;
    text-align: center;
    /* position: absolute; */
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    margin: 30px 30px;
    padding: 20px 30px;
    width: 60%;
}

.form h1 {
    font-family: 'Yanone Kaffeesatz', sans-serif;
    font-size: 30px;
}
input {
    width: 40%;
}

.backgroundFormSignIn  {
    display: flex;
    justify-content: center;
    background-image: url("https://dunked.cdn.speedyrails.net/assets/prod/76966/880x0_p1c4p1r26q1srqia761eoo8i8va.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    height: 100vh;
}

.backgroundFormSignUp  {
    display: flex;
    justify-content: center;
    background-image: url("https://dunked.cdn.speedyrails.net/assets/prod/76966/880x0_p1c4p1r26q1srqia761eoo8i8va.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    height: 100vh;
}

.form img {
    border-radius: 100px;
    width: 10%
}

.profile {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 30px;
    padding-bottom: 30px;
}

.profile img {
    width: 300px;
    height: auto;
    margin: 0
}

.profile-details {
    border: 1px solid grey;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    margin: 10px;
}

.profile-details p, .profile-details h3 {
    padding: 5px 10px
}

.profile-details p {

}
.create-trip-form {
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    margin: 50px 200px;
    padding: 20px 100px;
    border-radius: 10px;
    background-color: rgba(255, 255, 255, 0.7);
}

.create-trip {
    display: flex;
    background-image: url("https://theroadtrippers.fr/wp-content/uploads/2018/05/alice-hampson-5713-unsplash-1000x595.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    font-family: 'Yanone Kaffeesatz', sans-serif;
    font-size: 18px;
}
.one-trip .destination {
    display: flex;
    flex-direction: column;
    background-size: cover;
    background-position: bottom;
    justify-content: space-evenly;
    align-items: center;
    width: 70%;   
    height: 70vh;
    margin: 5px;
    border-radius: 10px;
    border: 1px solid rgb(156, 156, 156);
    padding: 40px 60px;
}
.one-trip {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.one-trip h1{
    font-family: 'Yanone Kaffeesatz', sans-serif;
    text-decoration: none;
    font-size: 40px;
    color: white;
    text-shadow: rgb(0, 0, 0) 5px 0px 5px;
}

.one-trip .destination p {
    text-align: center;
    margin: 60px;
    font-family: 'Yanone Kaffeesatz', sans-serif;
    font-size: 25px;
    color: white;
    text-shadow: rgb(0, 0, 0) 5px 0px 7px;
}

.activities-card {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    flex-direction: column;
    font-family: 'Yanone Kaffeesatz', sans-serif;
    font-size: 16px;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius:5px;
    border: 1px solid black;
    width: 100px;
    margin: 5px;
    text-align: center;
    background-color: white;
}
.activities-card p {
    padding: 2px
}

.all-activities {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    border: 1px solid black;
    background-color: rgb(255, 250, 244);
    border-radius: 10px;
    padding: 10px;
    margin: 5px

}

.all-activities h1, .points-of-interest h1 {
    margin: 0px 30px ;
    font-size: 35px;
    color: blue;
    text-shadow: none;
}



.points-of-interest {
    background-color: rgb(255, 250, 244);
    border: 1px solid black;
    border-radius: 10px;
    margin: 10px;
    padding: 5px;
}


.scroll-interest {
    display: flex;
    justify-content: center;
    flex-direction: column; 
    overflow-y: scroll; 
    height: 100px;
    background-color: rgb(255, 250, 244);
}

.one-trip table {
    max-width: 90vw;
}

.one-trip td {
    border: 1px solid black;
    
}

.trip-table {
    margin: 30px;
    text-align: center;
    display: flex;
    justify-content: center;
    width: 90vw;
    overflow-x: scroll;
}

.trip-table thead {
    background-color: rgb(182, 211, 255);
}

.trip-table tbody td {
    font-family: 'Yanone Kaffeesatz', sans-serif;
    height: 200px;
    padding: 10px 20px;
    align-items: center;
    align-content: center;
    justify-items: center;
    text-align: center;
    background-color: rgb(255, 242, 224);
}

.dashboard {
    display: flex;
    flex-direction: column;
}

.dashboard-head {
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin: 10px;
    padding: 10px;
    border-radius: 10px;
    background-color: rgb(253, 218, 123);
    border: 1px solid rgba(0, 9, 134, 0.774);
    font-family: 'Yanone Kaffeesatz', sans-serif;
    font-size: 18px;
}

.dashboard-head h1, .dashboard-head h3 {
    color: rgba(0, 9, 134, 0.774);
}
.dashboard-table {
    border: 1px solid rgba(0, 9, 134, 0.774);
    text-align: center;
    margin: 30px 60px 60px;
}

.dashboard-table thead {
    font-family: 'Yanone Kaffeesatz', sans-serif;
    font-size: 18px;
    background-color: rgb(182, 211, 255);
}
.dashboard-table tr, .dashboard-table td {
    border: 1px solid black;
    padding: 10px;
    margin: 20px;
}

.to-stylize {
    font-family: 'Yanone Kaffeesatz', sans-serif;
    font-size: 18px;
}
.footer {
    background-color:rgba(0, 9, 134, 0.774);
    display: flex;
    justify-content: space-between;
    font-family: 'Yanone Kaffeesatz', sans-serif;
    font-size: 18px;
    color: whitesmoke;
    padding: 10px;
}

.footer-info {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.social-network {
    display: flex;

}

.footer h2 {
    margin: 5px;
}
.footer img {
    width: 40px
}
.tourCards {
    display: flex;
    justify-content: space-evenly;
    margin: 5px;
    padding: 3px;
    border: 1px solid black;
    background-color: whitesmoke;
    z-index: 10;
    
}

.tourCards h2, .tourCards h3, .tourCards p {
    font-family: 'Yanone Kaffeesatz', sans-serif;
}

.tourCards h2 {
    font-size: 25px;
}

.tourCards h3 {
    font-size: 18px;
}

.tourCards img {
    width: 200px;
    height: 100%;
    margin: 3px
}

.tourDescription {
    font-family: 'Yanone Kaffeesatz', sans-serif;
    width: 60%;
}

.bookTour {
    display: flex;
    flex-direction: column;
    margin: 3px;
    /* justify-content: space-around ; */
}

.add-to-trip {
    display: flex;
    flex-direction: column;
    justify-content: space-around ;
}

.display-item {
    display: flex;
    justify-content: space-evenly;
    margin: 0px 15px;
    padding: 3px;
    border: 1px solid black;
    border-radius: 5px;
    background-color: whitesmoke;
    z-index: 10;
    position: absolute;
    width: 35%;
}

.back-button {
    margin: 10px;
    display: flex;
    z-index: 10;
    justify-content: center;
}

.tourDescription h2:hover {
    color: #0800a3;
}
.one-tour {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.one-tour h1 {
    font-family: 'Yanone Kaffeesatz', sans-serif;
    font-size: 30px;
    margin: 10px;
    padding: 15px 50px;
}

.one-tour h2 {
    font-family: 'Yanone Kaffeesatz', sans-serif;
    font-size: 25px;
    margin: 10px;
}


.one-tour p {
    font-family: 'Yanone Kaffeesatz', sans-serif;
    font-size: 23px;
    margin: 10px;
    padding: 20px;
    text-align: center;
    border-radius: 10px;
    background-color: rgb(255, 250, 244);
    border: 1px solid black
}
.one-tour img {
    width: 50%
}
